<template>
  <section class="section my-technologies">
    <div class="box">
      <div class="section__title">
        <div class="h1">My Technologies</div>
        <p>Here are listed the technology that will be identified by me in the development process</p>
      </div>
    </div>
    <div class="box">
      <div class="row" v-masonry transition-duration="0.3s" item-selector=".my-technologies__item">
        <div v-masonry-tile class="my-technologies__item col__md_4 col__xs_6" v-for="t in allTechnologies" :key="t.id">
          <img v-bind:src="t.icon" alt="">
          <div class="h4">{{ t.title }}</div>
          <p>{{ t.description }}</p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'

  export default {
    name: 'Technologies',
    mounted () {
      this.getAllTechnologies()
    },
    computed: {
      ...mapGetters([
        'allTechnologies'
      ])
    },
    methods: {
      ...mapActions([
        'getAllTechnologies'
      ])
    }
  }
</script>

<style lang="stylus" scoped>
  @require '../assets/styles/variables.styl'

  .my-technologies__item
    margin-bottom 1.6rem
    text-align left
    img
      width 88px
      border-bottom 2px solid #e0e0e0
      margin-bottom .8rem
    .h4
      margin-bottom 0
    p
      font-size .8rem
      color $textGrey

  @media screen and (max-width 580px)
    .my-technologies__item
      width 100%
      text-align center

</style>



